.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #fff;
  padding: 18px 0;
  font-size: 14px;
  line-height: 18px;

  &:before {
    content: '';
    width: 100%;
    height: 20px;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0));
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 2;
  }

  &__logo {
    width: 102px;
    display: inline-block;

    @media (max-width: 740px) {
      display: none;      
    }
  }
}